import uniNumberBox from '@/components/uni-number-box/uni-number-box.vue';
import instance from "../../api/index.js"
var cartlist=JSON.parse(localStorage.getItem('cartlist') || '[]')
export default {
	data(){
		return {
			islogin: '',
			token:"",
			flag: true, // 用于判断用户购物车是否有商品，没有商品为true，有商品为false
			cartlist: [], // 购物车商品信息
			allchecked: true//默认全选为true，因为后台数据没有是否选中的信息
		}
	},
	computed:{
		totalNum(){
			let totalNum = 0;
			this.cartlist.map(item => {
				item.flag ? totalNum += item.num : totalNum += 0
			})
			return totalNum
		},
		//计算选中商品的总价
		totalPrice() {
			let totalPrice = 0;
			this.cartlist.map(item => {
				item.flag ? totalPrice += item.shopCarNum * item.agentPrice : totalPrice += 0
			})
			return totalPrice
		}
	},
	onLoad() {
		this.token=uni.getStorageSync("token");
		this.cartlist=uni.getStorageSync("cartlist");
		if (this.token == '') {
			this.islogin = false;
		} else {
			this.islogin = true;
		}
		this.getcarlist()
	},
	onShow() {
		this.getcarlist()
	},
	methods: {
		create(){
			let arr=[]
			this.cartlist.forEach(item=>{
				arr.push(item.shopCarNo)
			})
			uni.navigateTo({
				url:"../center/myorder/createorder/createorder?list="+arr
			})
		},
		getcarlist(){
			instance({
				url:"/api/shopCar/queryMyShopCarInfo",
				data:{
					token:this.token
					},
					method:'POST',
					header:{
					   'content-type':'application/json',
				}
			}).then(res=>{
				console.log(res)
				this.cartlist=res.data.data[0].productList
				this.cartlist.forEach(item=>{
					item.flag=true
				})
				uni.setStorageSync("cartlist",res.data.data[0].productList)
			})
		},
		tologin() {
			uni.navigateTo({
				url: '../login/login'
			});
		},
		reduce(item){
			// console.log(item)
			let num = item.shopCarNum
			if (num > 1) {
				num -= 1
			} else {
				num = 1
				return
			}
			instance({
				url:"/api/shopCar/updateShopCarNum",
				data:{
					token:this.token,
					shopCarNo:item.shopCarNo,
					num:num
					},
					method:'POST',
					header:{
					   'content-type':'application/json',
				}
			}).then(res=>{
				// console.log(res)
				this.getcarlist()
			})
			
		},
		add(item) {
			let num = item.shopCarNum
			num += 1
			instance({
				url: '/api/shopCar/updateShopCarNum',
				data: {
					token:this.token,
					shopCarNo:item.shopCarNo,
					num:num
				},
				method:'POST',
					header:{
					   'content-type':'application/json',
				}
			}).then(res => {
				// console.log(res)
				this.getcarlist()
			})
			
		},
		del(item){
			instance({
				url: '/api/shopCar/deleteShopCar',
				data: {
					token:this.token,
					shopCarNo:item.shopCarNo
				},
				method:'POST',
					header:{
					   'content-type':'application/json',
				}
			}).then(res=>{
				// console.log(res)
				this.getcarlist()
			})
		},
		selected(item) {
			// console.log(item)
			item.flag = !item.flag
			if (item.flag=="false") {
				this.allchecked = false
			} else {
				const test = this.cartlist.every(item => {
					return item.flag === true
				})
				if (test) {
					this.allchecked = true
				} else {
					this.allchecked = false
				}
			}
			
		},
		// 全选按钮
		selectedall() {
			this.allchecked = !this.allchecked
			if (this.allchecked) {
				this.cartlist.map(item => {
					item.flag = true
				})
			} else {
				this.cartlist.map(item => {
					item.flag = false
				})
			}
			
		}
		
	}
}
